import React, { useState, useEffect } from 'react';
import { User, UpdateUserRequest, ranks } from '../../types';
import { useUserProfile, useAuth } from '../../hooks';
import { useManagement } from '../../contexts';
import { ThemeToggle } from '../../shared/components';
import { sanitizeInput } from '../../utils';
import './SettingsTab.css';

interface SettingsTabProps {
  userProfile: User | null;
  isAdmin: boolean;
}

interface EditingField {
  field: string;
  value: string;
}

const SettingsTab: React.FC<SettingsTabProps> = ({ userProfile: propUserProfile, isAdmin }) => {
  const { updateUserProfile, refetch, userProfile: hookUserProfile } = useUserProfile();
  const { user: firebaseUser } = useAuth();
  const { loadLocations } = useManagement();
  
  // Use the userProfile from the hook as it's the source of truth and gets updated by refetch
  const userProfile = hookUserProfile || propUserProfile;
  const [editingField, setEditingField] = useState<EditingField | null>(null);
  const [isUpdating, setIsUpdating] = useState(false);
  const [updateMessage, setUpdateMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);

  useEffect(() => {
    loadLocations();
  }, [loadLocations]);

  // Clear message after 3 seconds
  useEffect(() => {
    if (updateMessage) {
      const timer = setTimeout(() => {
        setUpdateMessage(null);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [updateMessage]);

  if (!userProfile) {
    return (
      <div className="settings-loading">
        <div className="loading-spinner"></div>
        <p>  ...</p>
      </div>
    );
  }

  const validateField = (field: string, value: string): string => {
    if (field === 'name') {
      return value.trim().length === 0 ? ' ' : '';
    } else if (field === 'personalNumber') {
      const numberValue = parseInt(value);
      if (isNaN(numberValue)) return '    ';
      if (value.length !== 7) return '    7 ';
      return '';
    } else if (field === 'phoneNumber') {
      const phoneRegex = /^[0-9]{10}$/;
      const cleanPhone = value.replace(/\D/g, '');
      return phoneRegex.test(cleanPhone) ? '' : '    10 ';
    }
    return '';
  };

  const handleStartEdit = (field: string, currentValue: string) => {
    setEditingField({ field, value: currentValue });
  };

  const handleCancelEdit = () => {
    setEditingField(null);
  };

  const handleSaveField = async () => {
    if (!editingField) return;

    const { field, value } = editingField;
    
    // Validation
    const error = validateField(field, value);

    if (error) {
      setUpdateMessage({ type: 'error', text: error });
      return;
    }

    setIsUpdating(true);
    setUpdateMessage(null);
    
    try {
      let updates: Partial<UpdateUserRequest> = {};
      
      if (field === 'personalNumber') {
        updates = { personalNumber: parseInt(value) };
      } else if (field === 'emailSubscribed') {
        updates = { emailSubscribed: value === 'true' };
      } else {
        updates = { [field]: sanitizeInput(value) };
      }
      
      const success = await updateUserProfile(updates as UpdateUserRequest);
      if (success) {
        setUpdateMessage({ type: 'success', text: '  !' });
        setEditingField(null);
        // Refresh user data from server to display the latest values
        await refetch();
      } else {
        setUpdateMessage({ type: 'error', text: '  ' });
      }
    } catch (error) {
      setUpdateMessage({ type: 'error', text: '  ' });
    } finally {
      setIsUpdating(false);
    }
  };

  const handleToggleEmailSubscription = (checked: boolean) => {
    if (!userProfile || isUpdating) return;
    
    const originalValue = userProfile.emailSubscribed ?? false;
    
    // If the new value is the same as original, cancel editing
    if (checked === originalValue) {
      setEditingField(null);
      return;
    }
    
    // Start editing mode for emailSubscribed field
    setEditingField({ field: 'emailSubscribed', value: checked.toString() });
  };

  const renderEditableField = (
    label: string,
    field: string,
    value: string,
    type: 'text' | 'select' = 'text',
    options?: { value: string; label: string }[]
  ) => {
    const isEditing = editingField?.field === field;
    
    return (
      <div className="settings-field">
        <label className="field-label">{label}</label>
        {isEditing ? (
          <div className="field-edit-container">
            {type === 'select' ? (
              <select
                className="field-input"
                value={editingField.value}
                onChange={(e) => setEditingField({ ...editingField, value: e.target.value })}
                disabled={isUpdating}
              >
                {options?.map(option => (
                  <option key={option.value} value={option.value}>
                    {option.label}
                  </option>
                ))}
              </select>
            ) : (
              <input
                type={type}
                className="field-input"
                value={editingField.value}
                onChange={(e) => setEditingField({ ...editingField, value: e.target.value })}
                disabled={isUpdating}
                autoFocus
              />
            )}
            <div className="field-actions">
              <button
                className="btn btn-sm btn-primary"
                onClick={handleSaveField}
                disabled={isUpdating}
              >
                {isUpdating ? '...' : ''}
              </button>
              <button
                className="btn btn-sm btn-ghost"
                onClick={handleCancelEdit}
                disabled={isUpdating}
              >
                
              </button>
            </div>
          </div>
        ) : (
          <div className="field-display">
            <span className="field-value">{value}</span>
            <button
              className="btn btn-sm btn-ghost field-edit-btn"
              onClick={() => handleStartEdit(field, value)}
              title={` ${label}`}
            >
              
            </button>
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="settings-tab">
      <div className="settings-header">
        <h1 className="settings-title"> </h1>
      </div>

      {updateMessage && (
        <div className={`settings-alert ${updateMessage.type === 'success' ? 'alert-success' : 'alert-error'}`}>
          {updateMessage.text}
        </div>
      )}

      <div className="settings-section">
        <h2 className="section-title"> </h2>
        <div className="settings-form">
          {renderEditableField(' ', 'name', userProfile.name)}
          {renderEditableField(' ', 'personalNumber', userProfile.personalNumber.toString())}
        </div>
      </div>

      <div className="settings-section">
        <h2 className="section-title"> </h2>
        <div className="settings-form">
          <div className="settings-field">
            <label className="field-label"> </label>
            <div className="field-display">
              <span className="field-value">{firebaseUser?.email || ' '}</span>
            </div>
          </div>
          {renderEditableField(' ', 'phoneNumber', userProfile.phoneNumber)}
        </div>
      </div>

      <div className="settings-section">
        <h2 className="section-title"> </h2>
        <div className="settings-form">
          {renderEditableField('', 'rank', userProfile.rank, 'select', ranks.map((rank: string) => ({ value: rank, label: rank })))}
          
          <div className="settings-field">
            <label className="field-label"></label>
            <div className="field-display">
              <span className="field-value">{userProfile.unit}</span>
            </div>
          </div>

          <div className="settings-field">
            <label className="field-label"></label>
            <div className="field-display">
              <span className="field-value">{userProfile.location}</span>
            </div>
          </div>

          <div className="settings-field">
            <label className="field-label"> </label>
            <div className="field-display">
              <span className="field-value role-badge" data-role={userProfile.isAdmin ? 'admin' : 'user'}>
                {userProfile.isAdmin ? ' ' : ' '}
              </span>
            </div>
          </div>
        </div>
      </div>

      <div className="settings-section">
        <h2 className="section-title"></h2>
        <div className="settings-form">
          <div className="settings-field">
            <label className="field-label"> </label>
            {editingField?.field === 'emailSubscribed' ? (
              <div className="field-edit-container">
                <div className="field-display notification-toggle-field">
                  <span className="notification-toggle-label">
                    {editingField.value === 'true' ? '' : ''}
                  </span>
                  <label className="notification-toggle-wrapper">
                    <input
                      type="checkbox"
                      checked={editingField.value === 'true'}
                      onChange={(e) => {
                        const newValue = e.target.checked;
                        const originalValue = userProfile.emailSubscribed ?? false;
                        
                        // If changed back to original value, exit edit mode
                        if (newValue === originalValue) {
                          setEditingField(null);
                        } else {
                          setEditingField({ ...editingField, value: newValue.toString() });
                        }
                      }}
                      disabled={isUpdating}
                      className="notification-toggle-input"
                    />
                    <span className="notification-toggle-slider"></span>
                  </label>
                </div>
                <div className="field-actions">
                  <button
                    className="btn btn-sm btn-primary"
                    onClick={handleSaveField}
                    disabled={isUpdating}
                  >
                    {isUpdating ? '...' : ''}
                  </button>
                  <button
                    className="btn btn-sm btn-ghost"
                    onClick={handleCancelEdit}
                    disabled={isUpdating}
                  >
                    
                  </button>
                </div>
              </div>
            ) : (
              <div className="field-display notification-toggle-field">
                <span className="notification-toggle-label">
                  {userProfile.emailSubscribed ? '' : ''}
                </span>
                <label className="notification-toggle-wrapper">
                  <input
                    type="checkbox"
                    checked={userProfile.emailSubscribed ?? false}
                    onChange={(e) => handleToggleEmailSubscription(e.target.checked)}
                    disabled={isUpdating}
                    className="notification-toggle-input"
                  />
                  <span className="notification-toggle-slider"></span>
                </label>
              </div>
            )}
          </div>
        </div>
      </div>

      <div className="settings-section">
        <h2 className="section-title"> </h2>
        <div className="settings-form">
          <div className="settings-field">
            <ThemeToggle 
              showLabel={true} 
              variant="default" 
              size="md"
              className="theme-toggle-settings" 
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default SettingsTab;
