import React, { useState } from 'react';
import Modal from '../../shared/components/Modal';

interface DeleteReceiptModalProps {
  isOpen: boolean;
  onClose: () => void;
  onConfirm: () => void;
  receiptId: string;
  isLoading?: boolean;
}

const DeleteReceiptModal: React.FC<DeleteReceiptModalProps> = ({
  isOpen,
  onClose,
  onConfirm,
  receiptId,
  isLoading = false
}) => {
  const [confirmationText, setConfirmationText] = useState('');
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (confirmationText.trim() !== '') {
      setError('  ""    ');
      return;
    }

    setError(null);
    onConfirm();
  };

  const handleClose = () => {
    setConfirmationText('');
    setError(null);
    onClose();
  };

  return (
    <Modal isOpen={isOpen} onClose={handleClose} title=" " size="md">
      <div style={{ direction: 'rtl', padding: '20px' }}>
        <div className="alert alert-danger" style={{ marginBottom: '20px' }}>
          <i className="fas fa-exclamation-triangle me-2"></i>
          <strong>:</strong>           .
        </div>

        <div style={{ marginBottom: '20px' }}>
          <p><strong> :</strong> {receiptId}</p>
          <p> / /    ?</p>
        </div>

        <form onSubmit={handleSubmit}>
          <div className="form-group" style={{ marginBottom: '20px' }}>
            <label className="form-label required">
               / <strong>""</strong>   :
            </label>
            <input
              type="text"
              className={`form-control ${error ? 'is-invalid' : ''}`}
              value={confirmationText}
              onChange={(e) => {
                setConfirmationText(e.target.value);
                if (error) setError(null);
              }}
              placeholder=" "
              required
              disabled={isLoading}
              autoFocus
            />
            {error && (
              <div className="invalid-feedback" style={{ display: 'block' }}>
                {error}
              </div>
            )}
          </div>

          <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '12px' }}>
            <button
              type="button"
              className="btn btn-secondary"
              onClick={handleClose}
              disabled={isLoading}
            >
              
            </button>
            <button
              type="submit"
              className="btn btn-danger"
              disabled={isLoading || confirmationText.trim() !== ''}
            >
              {isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin me-2"></i>
                  ...
                </>
              ) : (
                <>
                  <i className="fas fa-trash me-2"></i>
                   
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </Modal>
  );
};

export default DeleteReceiptModal;
