import React, { useState, useEffect } from 'react';
import { Item, CreateItemRequest } from '../../types';
import { useItems } from '../../hooks';
import { useManagement } from '../../contexts';
import { validateRequired, sanitizeInput, getConflictResolutionMessage } from '../../utils';
import { ConflictErrorModal, NotificationModal } from '../../shared/components';
import type { NotificationType } from '../../shared/components/NotificationModal';
import './ItemForm.css';

interface ItemFormProps {
  item: Item | null;
  isAdmin: boolean;
  onSuccess: () => void;
  onCancel: () => void;
}

interface FormErrors {
  name?: string;
  idNumber?: string;
  note?: string;
}

const ItemForm: React.FC<ItemFormProps> = ({ item, isAdmin, onSuccess, onCancel }) => {
  const { createItem, updateItem } = useItems();
  const { 
    itemNames, 
    locations,
    loading: managementLoading,
    loadItemNames,
    loadLocations
  } = useManagement();
  
  const [formData, setFormData] = useState<CreateItemRequest>({
    name: '',
    idNumber: '',
    note: '',
    isOperational: true,
    requiresReporting: false,
    allocatedLocationId: '',
  });
  const [quantity, setQuantity] = useState<number>(1);
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [conflictError, setConflictError] = useState<{
    isOpen: boolean;
    message: string;
    itemName: string;
  }>({
    isOpen: false,
    message: '',
    itemName: ''
  });

  const [notification, setNotification] = useState<{
    isOpen: boolean;
    type: NotificationType;
    message: string;
    title?: string;
  }>({
    isOpen: false,
    type: 'info',
    message: ''
  });

  const showNotification = (type: NotificationType, message: string, title?: string) => {
    setNotification({
      isOpen: true,
      type,
      message,
      title
    });
  };

  const closeNotification = () => {
    setNotification(prev => ({ ...prev, isOpen: false }));
  };

  // Load item names and locations when component mounts
  useEffect(() => {
    loadItemNames();
    loadLocations();
  }, [loadItemNames, loadLocations]);

  useEffect(() => {
    if (item) {
      setFormData({
        name: item.itemName?.name || '',
        idNumber: item.idNumber || '',
        note: item.note || '',
        isOperational: item.isOperational ?? true,
        requiresReporting: item.requiresReporting ?? false,
        allocatedLocationId: item.allocatedLocationId || '',
      });
      // Reset quantity to 1 when editing existing item
      setQuantity(1);
    } else {
      // Reset form data when creating new item
      setFormData({
        name: '',
        idNumber: '',
        note: '',
        isOperational: true,
        requiresReporting: false,
        allocatedLocationId: '',
      });
      // Reset quantity when creating new item
      setQuantity(1);
    }
  }, [item]);

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};

    if (!validateRequired(formData.name || '')) {
      newErrors.name = '  ';
    }

    if (formData.requiresReporting && !validateRequired(formData.idNumber || '')) {
      newErrors.idNumber = ' \'     ';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (field: keyof CreateItemRequest, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: field === 'note' || field === 'name' || field === 'allocatedLocationId' ? value : sanitizeInput(value),
    }));
    
    if (errors[field as keyof FormErrors]) {
      setErrors(prev => ({
        ...prev,
        [field]: undefined,
      }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);

    try {
      if (item?.id) {
        // For updates, exclude quantity from the request body and handle idNumber properly
        const { quantity: _, ...updateData } = formData;
        
        // If requiresReporting is false, remove idNumber entirely from the request body
        if (!updateData.requiresReporting) {
          delete updateData.idNumber;
        }
        
        // Convert empty allocatedLocationId to null
        if (updateData.allocatedLocationId === '') {
          updateData.allocatedLocationId = null;
        }
        
        const result = await updateItem(item.id, updateData);
        
        if (result.success) {
          onSuccess();
        } else if (result.isConflict) {
          // Show translated error (403/409) in modal
          setConflictError({
            isOpen: true,
            message: result.error || '      ',
            itemName: formData.name || '  '
          });
        } else {
          showNotification('error', result.error || '  ');
        }
      } else {
        // Validate admin permissions for creating new items
        if (!isAdmin) {
          showNotification('error', '     ');
          setIsSubmitting(false);
          return;
        }

        // For creating new items, prepare the request data
        const requestData = {
          ...formData,
          // Only include quantity if requiresReporting is false and quantity > 1
          ...(!formData.requiresReporting && quantity > 1 ? { quantity } : {})
        };
        
        // If requiresReporting is false, remove idNumber entirely from the request body
        if (!requestData.requiresReporting) {
          delete requestData.idNumber;
        }
        
        // Convert empty allocatedLocationId to null
        if (requestData.allocatedLocationId === '') {
          requestData.allocatedLocationId = null;
        }
        
        const result = await createItem(requestData as CreateItemRequest);
        
        if (result.success) {
          if (!formData.requiresReporting && quantity > 1) {
            showNotification('success', `  ${quantity} `);
          }
          onSuccess();
        } else if (result.isConflict) {
          setConflictError({
            isOpen: true,
            message: result.error || '   \'    ',
            itemName: formData.name || '  '
          });
        } else {
          showNotification('error', result.error || '  ');
        }
      }
    } catch (error) {
      // Removed console.error to avoid noisy logs
      showNotification('error', '  ');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <>
      <div style={{
        background: 'rgba(255, 255, 255, 0.05)',
        borderRadius: '16px',
        border: '1px solid rgba(255, 255, 255, 0.1)',
        padding: '32px',
        backdropFilter: 'blur(10px)',
        maxWidth: '600px',
        margin: '0 auto'
      }}>
        {!item && !isAdmin && (
          <div style={{
            background: 'linear-gradient(135deg, rgba(245, 158, 11, 0.1), rgba(217, 119, 6, 0.05))',
            borderRadius: '12px',
            border: '1px solid rgba(245, 158, 11, 0.2)',
            padding: '16px',
            marginBottom: '24px',
            display: 'flex',
            alignItems: 'center',
            gap: '12px'
          }}>
            <i className="fas fa-exclamation-triangle" style={{ color: '#f59e0b', fontSize: '16px' }}></i>
            <div>
              <strong style={{ color: 'rgba(255, 255, 255, 0.9)' }}>:</strong>
              <span style={{ color: 'rgba(255, 255, 255, 0.8)', marginRight: '8px' }}>
                      .
              </span>
            </div>
          </div>
        )}
        
        <form onSubmit={handleSubmit}>
          <div style={{ marginBottom: '24px' }}>
            <label style={{
              display: 'block',
              fontSize: '14px',
              fontWeight: '600',
              color: 'var(--color-text)',
              marginBottom: '8px'
            }}>
                <span style={{ color: '#ef4444' }}>*</span>
            </label>
            <select 
              value={formData.name} 
              onChange={e => handleInputChange('name', e.target.value)} 
              required
              disabled={managementLoading}
              style={{
                width: '100%',
                padding: '12px 16px',
                background: 'var(--color-surface)',
                border: `1px solid ${errors.name ? '#ef4444' : 'var(--color-border)'}`,
                borderRadius: '8px',
                color: 'var(--color-text)',
                fontSize: '14px',
                transition: 'all 0.2s ease',
                outline: 'none',
                appearance: 'none',
                paddingLeft: '16px',
                cursor: 'pointer'
              }}
              onFocus={(e) => {
                e.target.style.borderColor = 'var(--color-primary)';
                e.target.style.background = 'var(--color-bg-hover)';
              }}
              onBlur={(e) => {
                e.target.style.borderColor = errors.name ? '#ef4444' : 'var(--color-border)';
                e.target.style.background = 'var(--color-surface)';
              }}
            >
              <option value="" style={{
                background: 'var(--color-surface)',
                color: 'var(--color-text-muted)'
              }}>
                  
              </option>
              {itemNames.map(itemName => (
                <option 
                  key={itemName.id} 
                  value={itemName.name}
                  style={{
                    background: 'var(--color-surface)',
                    color: 'var(--color-text)'
                  }}
                >
                  {itemName.name}
                </option>
              ))}
            </select>
            {errors.name && (
              <div style={{
                color: '#ef4444',
                fontSize: '12px',
                marginTop: '4px',
                display: 'flex',
                alignItems: 'center',
                gap: '4px'
              }}>
                <i className="fas fa-exclamation-circle"></i>
                {errors.name}
              </div>
            )}
          </div>

          <div style={{ marginBottom: '24px' }}>
            <label style={{
              display: 'block',
              fontSize: '14px',
              fontWeight: '600',
              color: 'var(--color-text)',
              marginBottom: '8px'
            }}>
                ()
            </label>
            <select 
              value={formData.allocatedLocationId || ''} 
              onChange={e => handleInputChange('allocatedLocationId', e.target.value)} 
              disabled={managementLoading}
              style={{
                width: '100%',
                padding: '12px 16px',
                background: 'var(--color-surface)',
                border: '1px solid var(--color-border)',
                borderRadius: '8px',
                color: 'var(--color-text)',
                fontSize: '14px',
                transition: 'all 0.2s ease',
                outline: 'none',
                appearance: 'none',
                paddingLeft: '16px',
                cursor: 'pointer'
              }}
              onFocus={(e) => {
                (e.target as HTMLSelectElement).style.borderColor = 'var(--color-primary)';
                (e.target as HTMLSelectElement).style.background = 'var(--color-bg-hover)';
              }}
              onBlur={(e) => {
                (e.target as HTMLSelectElement).style.borderColor = 'var(--color-border)';
                (e.target as HTMLSelectElement).style.background = 'var(--color-surface)';
              }}
            >
              <option value="" style={{
                background: 'var(--color-surface)',
                color: 'var(--color-text-muted)'
              }}>
                  
              </option>
              {locations.map(location => (
                <option 
                  key={location.id} 
                  value={location.id}
                  style={{
                    background: 'var(--color-surface)',
                    color: 'var(--color-text)'
                  }}
                >
                  {location.name}
                </option>
              ))}
            </select>
          </div>

        <div style={{ marginBottom: '24px' }}>
          <div style={{
            background: 'rgba(255, 255, 255, 0.05)',
            borderRadius: '12px',
            border: '1px solid rgba(255, 255, 255, 0.1)',
            padding: '16px',
            transition: 'all 0.2s ease'
          }}>
            <label style={{
              display: 'flex',
              alignItems: 'center',
              cursor: 'pointer',
              gap: '12px'
            }}>
              <div style={{ position: 'relative' }}>
                <input
                  type="checkbox"
                  checked={formData.isOperational}
                  onChange={e => setFormData(prev => ({ ...prev, isOperational: e.target.checked }))}
                  style={{
                    position: 'absolute',
                    opacity: 0,
                    cursor: 'pointer'
                  }}
                />
                <div style={{
                  width: '20px',
                  height: '20px',
                  borderRadius: '4px',
                  border: '2px solid rgba(255, 255, 255, 0.3)',
                  background: formData.isOperational ? 'linear-gradient(135deg, #10b981, #059669)' : 'rgba(255, 255, 255, 0.1)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  transition: 'all 0.2s ease'
                }}>
                  {formData.isOperational && (
                    <i className="fas fa-check" style={{ 
                      color: 'white', 
                      fontSize: '12px' 
                    }}></i>
                  )}
                </div>
              </div>
              <div>
                <div style={{
                  color: 'rgba(255, 255, 255, 0.9)',
                  fontWeight: '600',
                  fontSize: '14px'
                }}>
                  ?
                </div>
                <div style={{
                  color: 'rgba(255, 255, 255, 0.6)',
                  fontSize: '12px'
                }}>
                    ?
                </div>
              </div>
            </label>
          </div>
        </div>

        <div style={{ marginBottom: '24px' }}>
          <div style={{
            background: 'rgba(255, 255, 255, 0.05)',
            borderRadius: '12px',
            border: '1px solid rgba(255, 255, 255, 0.1)',
            padding: '16px',
            transition: 'all 0.2s ease'
          }}>
            <label style={{
              display: 'flex',
              alignItems: 'center',
              cursor: 'pointer',
              gap: '12px'
            }}>
              <div style={{ position: 'relative' }}>
                <input
                  type="checkbox"
                  checked={formData.requiresReporting}
                  onChange={e => setFormData(prev => ({ 
                    ...prev, 
                    requiresReporting: e.target.checked,
                    idNumber: e.target.checked ? prev.idNumber : ''
                  }))}
                  style={{
                    position: 'absolute',
                    opacity: 0,
                    cursor: 'pointer'
                  }}
                />
                <div style={{
                  width: '20px',
                  height: '20px',
                  borderRadius: '4px',
                  border: '2px solid rgba(255, 255, 255, 0.3)',
                  background: formData.requiresReporting ? 'linear-gradient(135deg, #3b82f6, #1d4ed8)' : 'rgba(255, 255, 255, 0.1)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  transition: 'all 0.2s ease'
                }}>
                  {formData.requiresReporting && (
                    <i className="fas fa-check" style={{ 
                      color: 'white', 
                      fontSize: '12px' 
                    }}></i>
                  )}
                </div>
              </div>
              <div>
                <div style={{
                  color: 'rgba(255, 255, 255, 0.9)',
                  fontWeight: '600',
                  fontSize: '14px'
                }}>
                  ?
                </div>
                <div style={{
                  color: 'rgba(255, 255, 255, 0.6)',
                  fontSize: '12px'
                }}>
                     ?
                </div>
              </div>
            </label>
          </div>
        </div>

        {/* ID Number field - only when requiresReporting is true */}
        {formData.requiresReporting && (
          <div style={{ marginBottom: '24px' }}>
            <label style={{
              display: 'block',
              fontSize: '14px',
              fontWeight: '600',
              color: 'rgba(255, 255, 255, 0.9)',
              marginBottom: '8px'
            }}>
               ' <span style={{ color: '#ef4444' }}>*</span>
            </label>
            <input 
              type="text"
              value={formData.idNumber || ''} 
              onChange={e => handleInputChange('idNumber', e.target.value)} 
              placeholder="  '"
              required={formData.requiresReporting}
              style={{
                width: '100%',
                padding: '12px 16px',
                background: 'rgba(255, 255, 255, 0.1)',
                border: `1px solid ${errors.idNumber ? '#ef4444' : 'rgba(255, 255, 255, 0.2)'}`,
                borderRadius: '8px',
                color: 'rgba(255, 255, 255, 0.9)',
                fontSize: '14px',
                transition: 'all 0.2s ease',
                outline: 'none'
              }}
              onFocus={(e) => {
                e.target.style.borderColor = 'rgba(59, 130, 246, 0.5)';
                e.target.style.background = 'rgba(255, 255, 255, 0.15)';
              }}
              onBlur={(e) => {
                e.target.style.borderColor = errors.idNumber ? '#ef4444' : 'rgba(255, 255, 255, 0.2)';
                e.target.style.background = 'rgba(255, 255, 255, 0.1)';
              }}
            />
            {errors.idNumber && (
              <div style={{
                color: '#ef4444',
                fontSize: '12px',
                marginTop: '4px',
                display: 'flex',
                alignItems: 'center',
                gap: '4px'
              }}>
                <i className="fas fa-exclamation-circle"></i>
                {errors.idNumber}
              </div>
            )}
          </div>
        )}

        {/* Quantity selector - only for new items and when requiresReporting is false */}
        {!item && !formData.requiresReporting && (
          <div style={{ marginBottom: '24px' }}>
            <label style={{
              display: 'block',
              fontSize: '14px',
              fontWeight: '600',
              color: 'rgba(255, 255, 255, 0.9)',
              marginBottom: '8px'
            }}>
               
            </label>
            <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
              <input
                type="number"
                value={quantity}
                onChange={(e) => {
                  const value = parseInt(e.target.value) || 1;
                  setQuantity(Math.max(1, Math.min(100, value)));
                }}
                min="1"
                max="100"
                placeholder="1"
                style={{
                  width: '100px',
                  padding: '12px 16px',
                  background: 'rgba(255, 255, 255, 0.1)',
                  border: '1px solid rgba(255, 255, 255, 0.2)',
                  borderRadius: '8px',
                  color: 'rgba(255, 255, 255, 0.9)',
                  fontSize: '14px',
                  transition: 'all 0.2s ease',
                  outline: 'none',
                  textAlign: 'center',
                  direction: 'ltr'
                }}
                onFocus={(e) => {
                  e.target.style.borderColor = 'rgba(59, 130, 246, 0.5)';
                  e.target.style.background = 'rgba(255, 255, 255, 0.15)';
                }}
                onBlur={(e) => {
                  e.target.style.borderColor = 'rgba(255, 255, 255, 0.2)';
                  e.target.style.background = 'rgba(255, 255, 255, 0.1)';
                }}
              />
              <span style={{ 
                color: 'rgba(255, 255, 255, 0.6)', 
                fontSize: '13px' 
              }}>
                 (1-100)
              </span>
            </div>
          </div>
        )}
        
        <div style={{ marginBottom: '24px' }}>
          <label style={{
            display: 'block',
            fontSize: '14px',
            fontWeight: '600',
            color: 'rgba(255, 255, 255, 0.9)',
            marginBottom: '8px'
          }}>
            
          </label>
          <textarea 
            value={formData.note || ''} 
            onChange={e => handleInputChange('note', e.target.value)} 
            rows={3}
            style={{
              width: '100%',
              padding: '12px 16px',
              background: 'rgba(255, 255, 255, 0.1)',
              border: `1px solid ${errors.note ? '#ef4444' : 'rgba(255, 255, 255, 0.2)'}`,
              borderRadius: '8px',
              color: 'rgba(255, 255, 255, 0.9)',
              fontSize: '14px',
              transition: 'all 0.2s ease',
              outline: 'none',
              resize: 'vertical',
              minHeight: '80px',
              maxHeight: '120px',
              fontFamily: 'inherit'
            }}
            onFocus={(e) => {
              (e.target as HTMLTextAreaElement).style.borderColor = 'rgba(59, 130, 246, 0.5)';
              (e.target as HTMLTextAreaElement).style.background = 'rgba(255, 255, 255, 0.15)';
            }}
            onBlur={(e) => {
              (e.target as HTMLTextAreaElement).style.borderColor = errors.note ? '#ef4444' : 'rgba(255, 255, 255, 0.2)';
              (e.target as HTMLTextAreaElement).style.background = 'rgba(255, 255, 255, 0.1)';
            }}
          />
          {errors.note && (
            <div style={{
              color: '#ef4444',
              fontSize: '12px',
              marginTop: '4px',
              display: 'flex',
              alignItems: 'center',
              gap: '4px'
            }}>
              <i className="fas fa-exclamation-circle"></i>
              {errors.note}
            </div>
          )}
        </div>
        
          <div style={{
            display: 'flex',
            justifyContent: 'flex-end',
            gap: '12px',
            marginTop: '32px',
            paddingTop: '24px',
            borderTop: '1px solid rgba(255, 255, 255, 0.1)'
          }}>
            <button 
              type="button" 
              onClick={onCancel}
              style={{
                background: 'rgba(255, 255, 255, 0.1)',
                border: '1px solid rgba(255, 255, 255, 0.2)',
                borderRadius: '8px',
                padding: '12px 24px',
                color: 'rgba(255, 255, 255, 0.8)',
                fontSize: '14px',
                fontWeight: '600',
                cursor: 'pointer',
                transition: 'all 0.2s ease'
              }}
              onMouseEnter={(e) => {
                (e.target as HTMLButtonElement).style.background = 'rgba(255, 255, 255, 0.15)';
              }}
              onMouseLeave={(e) => {
                (e.target as HTMLButtonElement).style.background = 'rgba(255, 255, 255, 0.1)';
              }}
            >
              
            </button>
            <button 
              type="submit" 
              disabled={isSubmitting}
              style={{
                background: isSubmitting ? 'rgba(255, 255, 255, 0.1)' : 'linear-gradient(135deg, #3b82f6, #1d4ed8)',
                border: 'none',
                borderRadius: '8px',
                padding: '12px 24px',
                color: 'white',
                fontSize: '14px',
                fontWeight: '600',
                cursor: isSubmitting ? 'not-allowed' : 'pointer',
                transition: 'all 0.2s ease',
                boxShadow: isSubmitting ? 'none' : '0 4px 16px rgba(59, 130, 246, 0.3)'
              }}
            >
              {isSubmitting ? '...' : (
                item ? '' : (
                  (!formData.requiresReporting && quantity > 1) ? ` ${quantity} ` : ' '
                )
              )}
            </button>
          </div>
        </form>
      </div>

      <ConflictErrorModal
        isOpen={conflictError.isOpen}
        onClose={() => setConflictError({ isOpen: false, message: '', itemName: '' })}
        title={`     "${conflictError.itemName}"`}
        message={conflictError.message}
        resolutionMessage={getConflictResolutionMessage('item')}
        type="item"
      />

      <NotificationModal
        isOpen={notification.isOpen}
        onClose={closeNotification}
        type={notification.type}
        message={notification.message}
        title={notification.title}
      />
    </>
  );
};

export default ItemForm;
