import { ID } from './common.types';

export const ranks = [
  '', '"', '', '"', '"', '"', '"', '"', '"',
  '"', '"', '"', '"', '', '', '"', '"',
  '"', '"', '', '"',
] as const;

export type Rank = typeof ranks[number];

export interface User {
  id: ID;
  name: string;
  personalNumber: number;
  phoneNumber: string;
  location: string;
  unit: string;
  rank: string;
  isAdmin: boolean;
  isAdminInUnit?: boolean;
  emailSubscribed?: boolean;
  firebaseUid?: string;
  createdAt: string;
  updatedAt: string;
}

export interface CreateUserRequest {
  name: string;
  personalNumber: number;
  phoneNumber: string;
  rank: string;
  firebaseUid?: string;
  locationId?: string; // Made optional since users can't choose location during registration
}

export interface UpdateUserRequest {
  name?: string;
  personalNumber?: number;
  phoneNumber?: string;
  rank?: string;
  isAdmin?: boolean;
  isAdminInUnit?: boolean;
  emailSubscribed?: boolean;
  firebaseUid?: string;
  locationId?: string;
}
