import React, { useState } from 'react';
import { Modal, ConflictErrorModal, SmartPagination, LoadingSpinner, NotificationModal, SelectionToolbar, SearchInput } from '../../shared/components';
import { paginate } from '../../utils';
import { UI_CONFIG } from '../../config/app.config';
import { LocationEntity, UnitEntity } from '../../types';
import { NotificationType } from '../../shared/components/NotificationModal';
import { useModernTableSelection } from '../../hooks';

interface LocationsTableProps {
  locations: LocationEntity[];
  units: UnitEntity[];
  loading: boolean;
  error: string | null;
  onAdd: (data: { name: string; unitId: string }) => Promise<{ success: boolean; error?: string; isConflict?: boolean }>;
  onEdit: (id: string, data: { name?: string; unitId?: string }) => Promise<{ success: boolean; error?: string; isConflict?: boolean }>;
  onDelete: (ids: string[]) => Promise<{ success: boolean; error?: string }>;
  onRefresh: () => void;
}

export const LocationsTable: React.FC<LocationsTableProps> = ({
  locations,
  units,
  loading,
  error,
  onAdd,
  onEdit,
  onDelete,
  onRefresh,
}) => {
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [editingLocation, setEditingLocation] = useState<LocationEntity | null>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [newLocationData, setNewLocationData] = useState({ name: '', unitId: '' });
  const [editLocationData, setEditLocationData] = useState({ name: '', unitId: '' });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [sortConfig, setSortConfig] = useState<{
    key: keyof LocationEntity | 'unitName';
    direction: 'asc' | 'desc';
  } | null>(null);
  const [conflictError, setConflictError] = useState({
    isOpen: false,
    message: '',
    itemName: '',
  });
  const [notification, setNotification] = useState<{
    isOpen: boolean;
    message: string;
    type: NotificationType;
  }>({
    isOpen: false,
    message: '',
    type: 'error',
  });

  // Helper function to get unit name
  const getUnitName = (unitId: string) => {
    const unit = units.find(u => u.id === unitId);
    return unit ? unit.name : '  ';
  };

  // Filter locations
  const filteredLocations = locations.filter(location =>
    location.name.toLowerCase().normalize('NFC').includes(searchTerm.toLowerCase().normalize('NFC')) ||
    getUnitName(location.unitId).toLowerCase().normalize('NFC').includes(searchTerm.toLowerCase().normalize('NFC'))
  );

  // Sort locations
  const sortedLocations = [...filteredLocations].sort((a, b) => {
    if (!sortConfig) return 0;

    const { key, direction } = sortConfig;
    let aValue: any;
    let bValue: any;

    if (key === 'unitName') {
      aValue = getUnitName(a.unitId);
      bValue = getUnitName(b.unitId);
    } else if (key === 'createdAt' || key === 'updatedAt') {
      aValue = new Date(a[key]).getTime();
      bValue = new Date(b[key]).getTime();
    } else {
      aValue = a[key];
      bValue = b[key];
    }

    // Handle string sorting
    if (typeof aValue === 'string' && typeof bValue === 'string') {
      return direction === 'asc' 
        ? aValue.localeCompare(bValue, 'he') 
        : bValue.localeCompare(aValue, 'he');
    }

    // Handle number sorting
    if (direction === 'asc') {
      return aValue < bValue ? -1 : aValue > bValue ? 1 : 0;
    } else {
      return aValue > bValue ? -1 : aValue < bValue ? 1 : 0;
    }
  });

  const { paginatedItems, totalPages } = paginate(
    sortedLocations,
    currentPage,
    UI_CONFIG.TABLE_PAGE_SIZE
  );

  // Modern selection hook - use all sorted items for selection, not just paginated
  const {
    selectedIds,
    isSelected,
    toggleSelection,
    clearSelection,
    selectAll,
    selectedCount
  } = useModernTableSelection({
    items: sortedLocations,
    multiSelect: true
  });

  const handleSort = (key: keyof LocationEntity | 'unitName') => {
    let direction: 'asc' | 'desc' = 'asc';
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
    setCurrentPage(1); // Reset to first page when sorting
  };

  const resetAddForm = () => {
    setNewLocationData({ name: '', unitId: '' });
    setIsAddModalOpen(false);
  };

  const resetEditForm = () => {
    setEditLocationData({ name: '', unitId: '' });
    setEditingLocation(null);
  };

  const handleAdd = async () => {
    if (!newLocationData.name.trim() || !newLocationData.unitId) return;

    setIsSubmitting(true);
    try {
      const result = await onAdd(newLocationData);
      if (result.success) {
        resetAddForm();
      } else if (result.isConflict) {
        setConflictError({
          isOpen: true,
          message: result.error || '       ',
          itemName: newLocationData.name,
        });
      } else {
        setNotification({
          isOpen: true,
          message: result.error || '  ',
          type: 'error',
        });
      }
    } catch (error) {
      setNotification({
        isOpen: true,
        message: '  ',
        type: 'error',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleEdit = async () => {
    if (!editingLocation || (!editLocationData.name.trim() && !editLocationData.unitId)) return;

    setIsSubmitting(true);
    try {
      const updateData: { name?: string; unitId?: string } = {};
      if (editLocationData.name.trim()) updateData.name = editLocationData.name.trim();
      if (editLocationData.unitId) updateData.unitId = editLocationData.unitId;

      const result = await onEdit(editingLocation.id, updateData);
      if (result.success) {
        resetEditForm();
      } else if (result.isConflict) {
        setConflictError({
          isOpen: true,
          message: result.error || '       ',
          itemName: editLocationData.name || editingLocation.name,
        });
      } else {
        setNotification({
          isOpen: true,
          message: result.error || '  ',
          type: 'error',
        });
      }
    } catch (error) {
      setNotification({
        isOpen: true,
        message: '  ',
        type: 'error',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleBulkDelete = async () => {
    if (selectedIds.length === 0) return;

    const confirmMessage = `     ${selectedIds.length} ?`;
    if (!window.confirm(confirmMessage)) return;

    setIsSubmitting(true);
    try {
      const result = await onDelete(selectedIds);
      if (result.success) {
        clearSelection();
      } else {
        setNotification({
          isOpen: true,
          message: result.error || '  ',
          type: 'error',
        });
      }
    } catch (error) {
      setNotification({
        isOpen: true,
        message: '  ',
        type: 'error',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  const openEditModal = (location: LocationEntity) => {
    setEditingLocation(location);
    setEditLocationData({ name: location.name, unitId: location.unitId });
  };

  if (error) {
    return (
      <div className="error-container">
        <p className="error-message">{error}</p>
        <button className="btn btn-primary" onClick={onRefresh}>
           
        </button>
      </div>
    );
  }

  return (
    <>
      {/* Compact Header with Actions */}
      <div className="management-header-compact">
        <div className="management-search-section">
          <SearchInput
            value={searchTerm}
            onChange={(value) => {
              setSearchTerm(value);
              setCurrentPage(1);
            }}
            placeholder="   ..."
            resultsCount={filteredLocations.length}
            resultsLabel=""
          />
        </div>
        
        <div className="management-actions-compact">
          <button
            className="btn btn-primary btn-sm"
            onClick={() => setIsAddModalOpen(true)}
            disabled={loading}
            title="  "
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              gap: '6px',
              padding: '0 16px'
            }}
          >
            <i className="fas fa-plus" style={{ fontSize: '13px' }}></i>
            <span style={{ fontSize: '13px', fontWeight: '600' }}> </span>
          </button>
        </div>
      </div>

      {/* Modern Selection Toolbar */}
      <SelectionToolbar
        selectedCount={selectedCount}
        totalCount={sortedLocations.length}
        onDelete={handleBulkDelete}
        onClear={clearSelection}
        onSelectAll={selectAll}
        isDeleting={isSubmitting}
        isVisible={selectedCount > 0}
      />

      {loading ? (
        <LoadingSpinner 
          message=" ..." 
          padding="80px 20px"
          minHeight="400px"
        />
      ) : (
        <>
          <div className="unified-table-container">
            <table className="unified-table">
              <thead>
                <tr>
                  <th 
                    className="unified-table-header unified-table-header-first sortable"
                    onClick={() => handleSort('name')}
                    title="    "
                    data-sorted={sortConfig?.key === 'name' ? 'true' : 'false'}
                  >
                    <div>
                      <span> </span>
                    </div>
                  </th>
                  <th 
                    className="unified-table-header unified-table-header-regular sortable"
                    onClick={() => handleSort('unitName')}
                    title="   "
                    data-sorted={sortConfig?.key === 'unitName' ? 'true' : 'false'}
                  >
                    <div>
                      <span></span>
                    </div>
                  </th>
                  <th 
                    className="unified-table-header unified-table-header-regular sortable"
                    onClick={() => handleSort('createdAt')}
                    title="    "
                    data-sorted={sortConfig?.key === 'createdAt' ? 'true' : 'false'}
                  >
                    <div>
                      <span> </span>
                    </div>
                  </th>
                  <th 
                    className="unified-table-header unified-table-header-regular sortable"
                    onClick={() => handleSort('updatedAt')}
                    title="    "
                    data-sorted={sortConfig?.key === 'updatedAt' ? 'true' : 'false'}
                  >
                    <div>
                      <span> </span>
                    </div>
                  </th>
                  <th className="unified-table-header unified-table-header-regular" style={{ width: '100px' }}>
                    <div>
                      <span></span>
                    </div>
                  </th>
                </tr>
              </thead>
              <tbody>
                {paginatedItems.map((location) => (
                  <tr 
                    key={location.id} 
                    className={`unified-table-row modern-table-row ${isSelected(location.id) ? 'selected' : ''}`}
                    onClick={(e) => toggleSelection(location.id, e)}
                    title=" "
                  >
                    <td className="unified-table-cell unified-table-cell-first">{location.name}</td>
                    <td className="unified-table-cell">{getUnitName(location.unitId)}</td>
                    <td className="unified-table-cell">{new Date(location.createdAt).toLocaleDateString('he-IL')}</td>
                    <td className="unified-table-cell">{new Date(location.updatedAt).toLocaleDateString('he-IL')}</td>
                    <td className="unified-table-cell unified-table-cell-actions">
                      <button
                        className="unified-action-btn unified-action-btn-primary unified-action-btn-sm"
                        onClick={(e) => {
                          e.stopPropagation();
                          openEditModal(location);
                        }}
                      >
                        
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {totalPages > 1 && (
            <div className="pagination-container">
              <SmartPagination
                currentPage={currentPage}
                totalPages={totalPages}
                onPageChange={setCurrentPage}
              />
            </div>
          )}
        </>
      )}

      {/* Add Location Modal */}
      <Modal
        isOpen={isAddModalOpen}
        onClose={resetAddForm}
        title="  "
      >
        <div className="form-group">
          <label className="form-label"> </label>
          <input
            type="text"
            className="form-control"
            value={newLocationData.name}
            onChange={(e) => setNewLocationData(prev => ({ ...prev, name: e.target.value }))}
            placeholder="  "
          />
        </div>
        <div className="form-group">
          <label className="form-label"></label>
          <select
            className="form-control"
            value={newLocationData.unitId}
            onChange={(e) => setNewLocationData(prev => ({ ...prev, unitId: e.target.value }))}
          >
            <option value=""> </option>
            {units.map(unit => (
              <option key={unit.id} value={unit.id}>{unit.name}</option>
            ))}
          </select>
        </div>
        <div className="btn-group btn-group-end">
          <button className="btn btn-ghost" onClick={resetAddForm}>
            
          </button>
          <button 
            className="btn btn-primary" 
            onClick={handleAdd}
            disabled={isSubmitting || !newLocationData.name.trim() || !newLocationData.unitId}
          >
            {isSubmitting ? '...' : ''}
          </button>
        </div>
      </Modal>

      {/* Edit Location Modal */}
      <Modal
        isOpen={!!editingLocation}
        onClose={resetEditForm}
        title=" "
      >
        <div className="form-group">
          <label className="form-label"> </label>
          <input
            type="text"
            className="form-control"
            value={editLocationData.name}
            onChange={(e) => setEditLocationData(prev => ({ ...prev, name: e.target.value }))}
            placeholder={editingLocation?.name}
          />
        </div>
        <div className="form-group">
          <label className="form-label"></label>
          <select
            className="form-control"
            value={editLocationData.unitId}
            onChange={(e) => setEditLocationData(prev => ({ ...prev, unitId: e.target.value }))}
          >
            <option value="">  </option>
            {units.map(unit => (
              <option key={unit.id} value={unit.id}>{unit.name}</option>
            ))}
          </select>
        </div>
        <div className="btn-group btn-group-end">
          <button className="btn btn-ghost" onClick={resetEditForm}>
            
          </button>
          <button 
            className="btn btn-primary" 
            onClick={handleEdit}
            disabled={isSubmitting || (!editLocationData.name.trim() && !editLocationData.unitId)}
          >
            {isSubmitting ? '...' : ''}
          </button>
        </div>
      </Modal>

      {/* Conflict Error Modal */}
      <ConflictErrorModal
        isOpen={conflictError.isOpen}
        onClose={() => setConflictError({ isOpen: false, message: '', itemName: '' })}
        title="  "
        message={conflictError.message}
        resolutionMessage="       ."
        type="item"
      />

      {/* Notification Modal */}
      <NotificationModal
        isOpen={notification.isOpen}
        onClose={() => setNotification({ isOpen: false, message: '', type: 'error' })}
        message={notification.message}
        type={notification.type}
      />
    </>
  );
};
