import React, { useState } from 'react';
import { ServerError, ConflictErrorModal, BulkDeleteErrorModal, SmartPagination, LoadingSpinner, NotificationModal, SearchInput, AdvancedFilter, AdvancedFilterCriteria } from '../../shared/components';
import { SelectionToolbar } from '../../shared/components';
import { useModernTableSelection } from '../../hooks';
import Modal from '../../shared/components/Modal';
import ItemForm from './ItemForm';
import { useItems } from '../../hooks';
import { Item, User } from '../../types';
import { paginate, getConflictResolutionMessage } from '../../utils';
import { UI_CONFIG } from '../../config/app.config';
import type { NotificationType } from '../../shared/components/NotificationModal';

interface ItemsTabProps {
  userProfile: User;
  isAdmin: boolean;
}

const ItemsTab: React.FC<ItemsTabProps> = ({ userProfile, isAdmin }) => {
  const { items, loading, error, refetch, deleteManyItems } = useItems();
  const [selectedItem, setSelectedItem] = useState<Item | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortConfig, setSortConfig] = useState<{
    key: string;
    direction: 'asc' | 'desc';
  } | null>(null);
  const [advancedFilters, setAdvancedFilters] = useState<AdvancedFilterCriteria>({});
  const [isAdvancedFilterOpen, setIsAdvancedFilterOpen] = useState(false);
  const [conflictError, setConflictError] = useState<{
    isOpen: boolean;
    message: string;
    itemName: string;
  }>({
    isOpen: false,
    message: '',
    itemName: '',
  });

  const [bulkDeleteError, setBulkDeleteError] = useState<{
    isOpen: boolean;
    message: string;
    deletedCount: number;
    totalCount: number;
    errors: string[];
  }>({
    isOpen: false,
    message: '',
    deletedCount: 0,
    totalCount: 0,
    errors: [],
  });

  const [notification, setNotification] = useState<{
    isOpen: boolean;
    type: NotificationType;
    message: string;
    title?: string;
  }>({
    isOpen: false,
    type: 'info',
    message: ''
  });

  const showNotification = (type: NotificationType, message: string, title?: string) => {
    setNotification({
      isOpen: true,
      type,
      message,
      title
    });
  };

  const closeNotification = () => {
    setNotification(prev => ({ ...prev, isOpen: false }));
  };

  const handleSort = (key: string) => {
    let direction: 'asc' | 'desc' = 'asc';
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
  };

  // Helper function to get receipt location
  const getReceiptLocation = (item: Item) => {
    return item.receiptInfo?.signedBy?.location?.name || '';
  };

  // Filter and sort items based on search term, advanced filters, and sort config
  const filteredAndSortedItems = (() => {
    let filtered = items.filter(item => {
      // Basic text search filter
      if (searchTerm) {
        const statusText = !item.isOperational ? '' : (item.isAvailable ?? false) ? '' : ' ';
        const locationText = item.allocatedLocation?.name || ' ';
        const receiptLocationText = getReceiptLocation(item);
        
        const matchesSearch = (item.itemName?.name && item.itemName.name.toLowerCase().includes(searchTerm.toLowerCase())) ||
          (item.idNumber && item.idNumber.toLowerCase().includes(searchTerm.toLowerCase())) ||
          (item.note && item.note.toLowerCase().includes(searchTerm.toLowerCase())) ||
          statusText.toLowerCase().includes(searchTerm.toLowerCase()) ||
          locationText.toLowerCase().includes(searchTerm.toLowerCase()) ||
          receiptLocationText.toLowerCase().includes(searchTerm.toLowerCase());
        
        if (!matchesSearch) return false;
      }

      // Advanced filters
      if (advancedFilters.itemNameId && item.nameId !== advancedFilters.itemNameId) {
        return false;
      }

      if (advancedFilters.allocatedLocationId && item.allocatedLocationId !== advancedFilters.allocatedLocationId) {
        return false;
      }

      if (advancedFilters.receiptLocationName) {
        const itemReceiptLocation = getReceiptLocation(item);
        if (itemReceiptLocation !== advancedFilters.receiptLocationName) {
          return false;
        }
      }

      if (advancedFilters.status) {
        const isOperational = item.isOperational;
        const isAvailable = item.isAvailable ?? false;
        
        switch (advancedFilters.status) {
          case 'operational':
            if (!isOperational) return false;
            break;
          case 'non-operational':
            if (isOperational) return false;
            break;
          case 'available':
            if (!isAvailable || !isOperational) return false;
            break;
          case 'not-available':
            if (isAvailable || !isOperational) return false;
            break;
        }
      }

      if (advancedFilters.hasIdNumber !== undefined) {
        const hasIdNumber = Boolean(item.idNumber && item.idNumber.trim());
        if (hasIdNumber !== advancedFilters.hasIdNumber) {
          return false;
        }
      }

      if (advancedFilters.noteText) {
        const itemNote = item.note || '';
        if (!itemNote.toLowerCase().includes(advancedFilters.noteText.toLowerCase())) {
          return false;
        }
      }

      return true;
    });

    if (sortConfig) {
      filtered = [...filtered].sort((a, b) => {
        let aValue: any;
        let bValue: any;

        switch (sortConfig.key) {
          case 'name':
            aValue = a.itemName?.name || '';
            bValue = b.itemName?.name || '';
            break;
          case 'idNumber':
            aValue = a.idNumber || '';
            bValue = b.idNumber || '';
            break;
          case 'allocatedLocation':
            aValue = a.allocatedLocation?.name || '';
            bValue = b.allocatedLocation?.name || '';
            break;
          case 'receiptLocation':
            aValue = getReceiptLocation(a) || '';
            bValue = getReceiptLocation(b) || '';
            break;
          case 'status':
            // Status priority:  (0) <   (1) <  (2)
            aValue = !a.isOperational ? 0 : (a.isAvailable ?? false) ? 2 : 1;
            bValue = !b.isOperational ? 0 : (b.isAvailable ?? false) ? 2 : 1;
            break;
          case 'note':
            aValue = a.note || '';
            bValue = b.note || '';
            break;
          case 'isOperational':
            aValue = a.isOperational;
            bValue = b.isOperational;
            break;
          case 'isAvailable':
            aValue = a.isAvailable ?? false;
            bValue = b.isAvailable ?? false;
            break;
          default:
            return 0;
        }

        if (typeof aValue === 'string' && typeof bValue === 'string') {
          return sortConfig.direction === 'asc' 
            ? aValue.localeCompare(bValue, 'he') 
            : bValue.localeCompare(aValue, 'he');
        }

        if (sortConfig.direction === 'asc') {
          return aValue < bValue ? -1 : aValue > bValue ? 1 : 0;
        } else {
          return aValue > bValue ? -1 : aValue < bValue ? 1 : 0;
        }
      });
    }

    return filtered;
  })();

  const { paginatedItems, totalPages } = paginate(
    filteredAndSortedItems,
    currentPage,
    UI_CONFIG.TABLE_PAGE_SIZE
  );

  // Modern selection hook - use all filtered items for selection, not just paginated
  const {
    selectedIds,
    isSelected,
    toggleSelection,
    clearSelection,
    selectAll,
    selectedCount
  } = useModernTableSelection({
    items: filteredAndSortedItems,
    multiSelect: true
  });

  const handleAddClick = () => {
    setSelectedItem(null);
    setIsModalOpen(true);
  };

  const handleItemClick = (item: Item) => {
    setSelectedItem(item);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedItem(null);
    refetch(); // Refresh the items list after modal closes
  };

  // Reset page when search term changes
  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(e.target.value);
    setCurrentPage(1);
    clearSelection();
  };

  // Handle advanced filter changes
  const handleAdvancedFiltersChange = (filters: AdvancedFilterCriteria) => {
    setAdvancedFilters(filters);
    setCurrentPage(1);
    clearSelection();
  };

  // Toggle advanced filter panel
  const handleAdvancedFilterToggle = () => {
    setIsAdvancedFilterOpen(!isAdvancedFilterOpen);
  };

  const handleBulkDelete = async () => {
    if (selectedIds.length === 0) return;

    const selectedItems = items.filter(item => selectedIds.includes(item.id));
    const itemNames = selectedItems.map(item => item.itemName?.name || ' ').join(', ');
    
    if (!window.confirm(`       ?\n\n${itemNames}\n\n(${selectedIds.length} )`)) return;

    const result = await deleteManyItems(selectedIds);
    if (result.success) {
      clearSelection();
    } else if (result.isConflict && result.bulkError) {
      // Show bulk delete error modal
      setBulkDeleteError({
        isOpen: true,
        message: result.bulkError.message,
        deletedCount: result.bulkError.deletedCount,
        totalCount: selectedIds.length,
        errors: result.bulkError.errors,
      });
      clearSelection();
    } else if (result.isConflict) {
      // Show regular conflict error modal
      setConflictError({
        isOpen: true,
        message: result.error || '   ',
        itemName: `${selectedIds.length} `,
      });
    } else {
      showNotification('error', `  : ${result.error || '  '}`);
    }
  };

  // Check if user has a location assigned
  if (!userProfile.location) {
    return (
      <div className="card">
        <div className="card-header">
          <h2 className="mb-0"></h2>
        </div>
        <div className="card-body">
          <div className="alert alert-warning">
            <h4>   </h4>
            <p>    .        .</p>
          </div>
        </div>
      </div>
    );
  }

  if (loading) {
    return (
      <LoadingSpinner message="  ..." />
    );
  }

  if (error) {
    return <ServerError />;
  }

  return (
    <>
      {/* Compact Header with Actions */}
      <div className="management-header-compact">
        <div className="management-search-section">
          <SearchInput
            value={searchTerm}
            onChange={(value) => handleSearchChange({ target: { value } } as React.ChangeEvent<HTMLInputElement>)}
            placeholder="   ,  ', ,   ..."
            resultsCount={filteredAndSortedItems.length}
            resultsLabel=""
          />
        </div>
        
        <div className="management-actions-compact">
          <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
            <button className="btn btn-primary btn-sm" onClick={handleAddClick}>
              <i className="fas fa-plus" style={{ marginLeft: '6px' }}></i>
                
            </button>
          </div>
        </div>
      </div>

      {/* Advanced Filter Component */}
      <AdvancedFilter
        items={items}
        filters={advancedFilters}
        onFiltersChange={handleAdvancedFiltersChange}
        isOpen={isAdvancedFilterOpen}
        onToggle={handleAdvancedFilterToggle}
        isAdmin={isAdmin}
        resultsCount={filteredAndSortedItems.length}
      />

      {/* Modern Selection Toolbar */}
      <SelectionToolbar
        selectedCount={selectedCount}
        totalCount={filteredAndSortedItems.length}
        onDelete={handleBulkDelete}
        onClear={clearSelection}
        onSelectAll={selectAll}
        isDeleting={false}
        isVisible={selectedCount > 0}
      />

        <div className="unified-table-container">
          <table className="unified-table">
            <thead>
              <tr>
                <th 
                  className="unified-table-header unified-table-header-first sortable"
                  onClick={() => handleSort('name')}
                  title="    "
                  data-sorted={sortConfig?.key === 'name' ? 'true' : 'false'}
                >
                  <div>
                    <span> </span>
                  </div>
                </th>
                <th 
                  className="unified-table-header unified-table-header-regular sortable"
                  onClick={() => handleSort('idNumber')}
                  title="    '"
                  data-sorted={sortConfig?.key === 'idNumber' ? 'true' : 'false'}
                >
                  <div>
                    <span> '</span>
                  </div>
                </th>
                <th 
                  className="unified-table-header unified-table-header-regular sortable"
                  onClick={() => handleSort('allocatedLocation')}
                  title="   "
                  data-sorted={sortConfig?.key === 'allocatedLocation' ? 'true' : 'false'}
                >
                  <div>
                    <span></span>
                  </div>
                </th>
                <th 
                  className="unified-table-header unified-table-header-regular sortable"
                  onClick={() => handleSort('receiptLocation')}
                  title="   "
                  data-sorted={sortConfig?.key === 'receiptLocation' ? 'true' : 'false'}
                >
                  <div>
                    <span></span>
                  </div>
                </th>
                <th 
                  className="unified-table-header unified-table-header-regular sortable"
                  onClick={() => handleSort('status')}
                  title="   "
                  data-sorted={sortConfig?.key === 'status' ? 'true' : 'false'}
                >
                  <div>
                    <span></span>
                  </div>
                </th>
                <th 
                  className="unified-table-header unified-table-header-regular sortable"
                  onClick={() => handleSort('note')}
                  title="   "
                  data-sorted={sortConfig?.key === 'note' ? 'true' : 'false'}
                >
                  <div>
                    <span></span>
                  </div>
                </th>
                <th className="unified-table-header unified-table-header-regular">
                  <div>
                    <span></span>
                  </div>
                </th>
              </tr>
            </thead>
            <tbody>
              {paginatedItems.map((item: Item, index: number) => (
                <tr 
                  key={item.id} 
                  className={`unified-table-row modern-table-row ${isSelected(item.id) ? 'selected' : ''}`}
                  onClick={(e) => toggleSelection(item.id, e)}
                  title=" "
                >
                  <td className="unified-table-cell unified-table-cell-first">{item.itemName?.name || ' '}</td>
                  <td className="unified-table-cell unified-table-cell-badge">
                    <span className={`unified-badge ${item.idNumber ? 'unified-badge-primary' : 'unified-badge-secondary'}`}>
                      {item.idNumber || ' '}
                    </span>
                  </td>
                  <td className="unified-table-cell">{item.allocatedLocation?.name || ' '}</td>
                  <td className="unified-table-cell">{getReceiptLocation(item)}</td>
                  <td className="unified-table-cell unified-table-cell-badge">
                    <span 
                      className={`unified-badge ${
                        !item.isOperational 
                          ? 'unified-badge-warning' 
                          : (item.isAvailable ?? false) 
                            ? 'unified-badge-success' 
                            : 'unified-badge-danger'
                      }`}
                    >
                      {!item.isOperational 
                        ? '' 
                        : (item.isAvailable ?? false) 
                          ? '' 
                          : ' '
                      }
                    </span>
                  </td>
                  <td className="unified-table-cell">{item.note || ' '}</td>
                  <td className="unified-table-cell unified-table-cell-actions">
                    <button 
                      className="unified-action-btn unified-action-btn-primary unified-action-btn-sm" 
                      onClick={(e) => {
                        e.stopPropagation();
                        handleItemClick(item);
                      }}
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {totalPages > 1 && (
          <SmartPagination
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={setCurrentPage}
          />
        )}

      <Modal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        title={selectedItem ? ' ' : '  '}
        size="md"
      >
        <ItemForm
          key={selectedItem?.id || 'new'}
          item={selectedItem}
          isAdmin={isAdmin}
          onSuccess={handleCloseModal}
          onCancel={handleCloseModal}
        />
      </Modal>

      <ConflictErrorModal
        isOpen={conflictError.isOpen}
        onClose={() => setConflictError({ isOpen: false, message: '', itemName: '' })}
        title={`     "${conflictError.itemName}"`}
        message={conflictError.message}
        resolutionMessage={getConflictResolutionMessage('item')}
        type="item"
      />

      <BulkDeleteErrorModal
        isOpen={bulkDeleteError.isOpen}
        onClose={() => setBulkDeleteError({ 
          isOpen: false, 
          message: '', 
          deletedCount: 0, 
          totalCount: 0, 
          errors: [] 
        })}
        title="   - "
        message={bulkDeleteError.message}
        deletedCount={bulkDeleteError.deletedCount}
        totalCount={bulkDeleteError.totalCount}
        errors={bulkDeleteError.errors}
        type="item"
      />

      <NotificationModal
        isOpen={notification.isOpen}
        onClose={closeNotification}
        type={notification.type}
        message={notification.message}
        title={notification.title}
      />
    </>
  );
};

export default ItemsTab;
